  border-color: #0000;
  border-radius: 50%;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.btn-up:hover, .btn-up:focus {
  filter: brightness(1.4);
  outline: none;
  animation: rotate-center .6s ease-in-out both;
}

.btn-up:before, .btn-up:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-up:before {
  width: 5px;
  height: 25px;
